export function generateAuthError(error) {
    switch (error) {
        case "INVALID_PASSWORD":
            return " E-mail  ";
        case "EMAIL_NOT_FOUND":
            return " E-mail  ";
        case "EMAIL_EXISTS":
            return " E-mail";
        case "INVALID_EMAIL":
            return "E-mail  ";
        default:
            return error;
    }
}
